/* Menu box styles */
.menu-box {
  border: 2px solid #333;      /* Box border */
  padding: 20px;               /* Padding inside the box */
  background-color: #f9f9f9;   /* Optional background color */
  border-radius: 8px;          /* Rounded corners */
  width: 300px;                /* Adjust the width of the box */
}

.menu-box h3 {
  margin: 10px 0;              /* Space between each h3 */
  font-weight: normal;         /* Adjust font weight */
  white-space: nowrap;         /* Prevent excessive spaces */
}

